@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap";body{background-color:#f9f2ed}*{font-family:"Roboto",system-ui !important;box-sizing:border-box;user-select:none}.App{display:flex;grid-template-columns:1fr 1fr;grid-column-gap:30px;width:1000px;margin:100px auto;background-color:#fff;padding:30px;box-shadow:0 10px 20px rgba(0,0,0,.05);border-radius:15px}.—Åonverter{display:inline-flex}.block{margin:20px}.block input{display:flex;border:1px solid rgba(0,0,0,.1);font-size:3rem;font-weight:bold;padding:20px 20px;letter-spacing:1.3px;outline:none;margin-top:20px;border-radius:30px;width:100%}.currencies{display:flex;list-style:none;padding:0;margin:0;border:1px solid rgba(0,0,0,.1);border-radius:30px;overflow:hidden}.currencies li{display:inline-flex;padding:10px 20px;border-right:1px solid rgba(0,0,0,.05);cursor:pointer;justify-content:center;flex:1}.currencies li:hover{background-color:rgba(0,0,0,.03)}.currencies li:active{background-color:rgba(0,0,0,.05)}.currencies li.active{background-color:#0e97aa;color:#fff}.currencies li:last-of-type{display:inline-flex;align-items:center;flex:0 0 50px;border-right:0}.currencies li:last-of-type svg{width:16px;height:16px}